.arena-member {
  padding-bottom: 0.4rem;
}

.text {
  text-shadow: var(--text-visibility);
  color: var(--text-color-inset-primary);
}

.arena-stats {
  margin: 0 auto;
  line-height: 1 2px;
}

.ted {
  line-height: 3px;
}

.tedh {
  line-height: 32px;
}
#\32v2 table {
  height: 20rem;
}
.table-container {
  overflow-x: hidden;
  max-width: 60rem;
  height: auto;
  text-shadow: var(--text-visibility);
  color: var(--text-color-inset-primary);
  margin: 0 auto;
}

.arena-table {
  margin: 0 auto;
  background-color: var(--main-bg-color-light);
}

table {
  width: 60rem;
  height: 30rem;
  align-items: center;
  color: var(--text-color-inset-primary);
  text-shadow: var(--text-visibility);
  font-family: 'Signika Negative', sans-serif;
  letter-spacing: 0.9px;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4), 8px 8px 15px rgba(0, 0, 0, 0.7);
}

.robdog-border {
  border: rgb(22, 48, 65) 2.1px solid !important;
}

.robdog-border td {
  border: rgb(22, 48, 65) 2.1px solid !important;
}

th {
  background-color: var(--main-bg-color-inset);
  vertical-align: middle;
  line-height: 35px;
}

td,
th {
  border: 2px solid var(--main-bg-color);
}

.rank {
  padding-bottom: 0.8rem;
  font-weight: bold;
  font-size: 20px;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid var(--main-bg-color); /* Blue */
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite;
  margin: 0 auto;
}

.table-hover tbody tr:hover td,
.table-hover tbody tr:hover th {
  color: rgb(160, 160, 160);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 1350px) {
  .table {
    max-width: 35rem;
  }
  .table-container {
    overflow-x: scroll;
    max-width: 45rem;
  }
  th,
  td {
    min-width: 120px;
  }
}
@media only screen and (max-width: 750px) {
  .table-container {
    overflow-x: scroll;
    max-width: 20rem;
  }
  .table {
    max-width: 1rem;
  }
  th,
  td {
    min-width: 100px;
  }

  .arena-stats {
    line-height: 10px;
  }
  th {
    line-height: 10px;
  }
  .arena-stats .ted {
    line-height: 0px;
  }

  .tedh {
    line-height: 14px;
  }
}
